
// Converted Variables

$space-1: .5rem !default;
$space-2: 1rem !default;
$space-3: 2rem !default;
$space-4: 4rem !default;




/* Margins */

.m0  { margin:        0 }

.mt0 { margin-top:    0 }

.mr0 { margin-right:  0 }

.mb0 { margin-bottom: 0 }


.ml0 { margin-left:   0 }

.mx0 { margin-left:   0; margin-right:  0 }

.my0 { margin-top:    0; margin-bottom: 0 }

.m1  { margin:        $space-1 }

.mt1 { margin-top:    $space-1 }

.mr1 { margin-right:  $space-1 }

.mb1 { margin-bottom: $space-1 }

.ml1 { margin-left:   $space-1 }

.mx1 { margin-left:   $space-1; margin-right:  $space-1 }

.my1 { margin-top:    $space-1; margin-bottom: $space-1 }

.m2  { margin:        $space-2 }

.mt2 { margin-top:    $space-2 }

.mr2 { margin-right:  $space-2 }

.mb2 { margin-bottom: $space-2 }

.ml2 { margin-left:   $space-2 }

.mx2 { margin-left:   $space-2; margin-right:  $space-2 }

.my2 { margin-top:    $space-2; margin-bottom: $space-2 }

.m3  { margin:        $space-3 }

.mt3 { margin-top:    $space-3 }

.mr3 { margin-right:  $space-3 }

.mb3 { margin-bottom: $space-3 }

.ml3 { margin-left:   $space-3 }

.mx3 { margin-left:   $space-3; margin-right:  $space-3 }

.my3 { margin-top:    $space-3; margin-bottom: $space-3 }

.m4  { margin:        $space-4 }

.mt4 { margin-top:    $space-4 }

.mr4 { margin-right:  $space-4 }

.mb4 { margin-bottom: $space-4 }

.ml4 { margin-left:   $space-4 }

.mx4 { margin-left:   $space-4; margin-right:  $space-4 }

.my4 { margin-top:    $space-4; margin-bottom: $space-4 }

.mxn1 { margin-left: -$space-1; margin-right: -$space-1; }

.mxn2 { margin-left: -$space-2; margin-right: -$space-2; }

.mxn3 { margin-left: -$space-3; margin-right: -$space-3; }

.mxn4 { margin-left: -$space-4; margin-right: -$space-4; }

.ml-auto { margin-left: auto }

.mr-auto { margin-right: auto }

.mx-auto { margin-left: auto; margin-right: auto; }


@media (max-width: 47.99em) {
  .m-m0  { margin:        0 }

  .m-mt0 { margin-top:    0 }

  .m-mr0 { margin-right:  0 }

  .m-mb0 { margin-bottom: 0 }


  .m-ml0 { margin-left:   0 }

  .m-mx0 { margin-left:   0; margin-right:  0 }

  .m-my0 { margin-top:    0; margin-bottom: 0 }

  .m-m1  { margin:        $space-1 }

  .m-mt1 { margin-top:    $space-1 }

  .m-mr1 { margin-right:  $space-1 }

  .m-mb1 { margin-bottom: $space-1 }

  .m-ml1 { margin-left:   $space-1 }

  .m-mx1 { margin-left:   $space-1; margin-right:  $space-1 }

  .m-my1 { margin-top:    $space-1; margin-bottom: $space-1 }

  .m-m2  { margin:        $space-2 }

  .m-mt2 { margin-top:    $space-2 }

  .m-mr2 { margin-right:  $space-2 }

  .m-mb2 { margin-bottom: $space-2 }

  .m-ml2 { margin-left:   $space-2 }

  .m-mx2 { margin-left:   $space-2; margin-right:  $space-2 }

  .m-my2 { margin-top:    $space-2; margin-bottom: $space-2 }

  .m-m3  { margin:        $space-3 }

  .m-mt3 { margin-top:    $space-3 }

  .m-mr3 { margin-right:  $space-3 }

  .m-mb3 { margin-bottom: $space-3 }

  .m-ml3 { margin-left:   $space-3 }

  .m-mx3 { margin-left:   $space-3; margin-right:  $space-3 }

  .m-my3 { margin-top:    $space-3; margin-bottom: $space-3 }

  .m-m4  { margin:        $space-4 }

  .m-mt4 { margin-top:    $space-4 }

  .m-mr4 { margin-right:  $space-4 }

  .m-mb4 { margin-bottom: $space-4 }

  .m-ml4 { margin-left:   $space-4 }

  .m-mx4 { margin-left:   $space-4; margin-right:  $space-4 }

  .m-my4 { margin-top:    $space-4; margin-bottom: $space-4 }

  .m-mxn1 { margin-left: -$space-1; margin-right: -$space-1; }

  .m-mxn2 { margin-left: -$space-2; margin-right: -$space-2; }

  .m-mxn3 { margin-left: -$space-3; margin-right: -$space-3; }

  .m-mxn4 { margin-left: -$space-4; margin-right: -$space-4; }

  .m-ml-auto { margin-left: auto }

  .m-mr-auto { margin-right: auto }

  .m-mx-auto { margin-left: auto; margin-right: auto; }

}